.HeaderRow, .PinglistCol {
  text-align: center;
}

.rulePosition {
	display: block;
	min-height: 14px;
}

.RulesCol h1:first-child {
	margin:0;
	padding:0;
}

.HeaderRow {
	background: #000;
	margin-bottom: 5px;
}

.HeaderContent {
	padding: 15px 0;
}


.App {
